  display: flex;
}

.navItems * {
  white-space: nowrap;
  color: var(--textColor);
  padding: .5rem 1.5rem;
  text-decoration: none;
}

.cart {
  background-color: var(--theme);
  color: #fff;
  border-radius: 5rem;
  font-family: monospace;
  font-weight: 500;
}

.cart:hover {
  background-color: green;
}

.body {
  background-color: var(--bodyBackground);
  padding-top: 1rem;
}

.filterBar {
  white-space: nowrap;
  color: #000;
  justify-content: space-between;
  width: 80%;
  margin: auto;
  font-family: monospace;
  transition: all 1.5s;
  display: flex;
  overflow: hidden;
}

@media screen and (width <= 90rem) {
  .filterBar {
    flex-wrap: wrap;
    width: 90%;
  }
}

.search {
  z-index: 5;
  width: 15rem;
  padding: 1px;
}

.overlay {
  backdrop-filter: blur(3px);
  z-index: 3;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 5rem;
  right: 0;
}

.searchInput {
  z-index: 5;
  border: 1px solid #fff;
  border-radius: 1rem;
  width: 15rem;
  padding: 0 1rem;
  transition: all 1s;
  position: absolute;
}

.searchInput:hover {
  border: 1px solid gray;
}

.searchResult {
  z-index: 5;
  border: 1px solid var(--theme);
  background-color: #fff;
  border-radius: 1rem;
  width: 80%;
  max-height: 40rem;
  margin: 2rem auto;
  transition: all 1s;
  position: absolute;
  overflow: hidden scroll;
}

.searchItem {
  padding: 2px 1rem;
  transition: all;
}

.searchItem:hover {
  background-color: var(--theme);
  color: #fff;
}

.filterBar button {
  color: #313131;
  border: 1px solid #ccc;
  border-radius: 1rem;
  margin: 1px;
  padding: 2px 1rem;
}

.filterBar button:hover {
  background-color: #cecece;
  transition: all .25s;
}

