  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.foodCard {
  background-color: var(--cardBackground);
  color: var(--textColor);
  border-radius: 7px;
  width: 15rem;
  min-width: 15rem;
  height: 21rem;
  margin: 1.5rem;
  overflow: hidden;
}

.fadeUpAnimation {
  animation: .5s linear fadeUp;
}

@keyframes fadeUp {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.loading {
  text-align: center;
  margin-top: 50%;
}

.foodCard:hover {
  transition: all .3s;
  translate: -.25rem -.25rem;
  box-shadow: 2rem 2rem 2rem gray;
}

.foodCard:not(:hover) {
  transition: all .2s;
