}

.foodItems {
  color: gray;
  margin-top: 1px;
  padding-left: 1rem;
}

.storeRating {
  color: #bdbdbd;
  margin-top: 1px;
  padding-left: 1rem;
  display: flex;
}

.storeRating div {
  white-space: nowrap;
  padding-right: .5rem;
}

.deliveryTime {
  margin-top: 1px;
  padding-left: 1rem;
}

