  width: 20rem;
  margin: auto;
  transition: all 1s;
}

.restaurantImage {
  object-fit: cover;
  opacity: 1;
  border-radius: 1rem;
  width: 40rem;
  height: 40rem;
  transition: all 1.5s;
  overflow: hidden;
}

.restaurantInfo .name {
  color: green;
  font-family: monospace;
  font-size: 2rem;
  font-weight: bold;
}

.aboutUs {
  background-color: var(--bodyBackground);
